//mixins for adaptiveness

//map of breakpoints - basically just like an object to more efficient code
$breakpoints: (
    "xs" : 350px,
    "sm" : 500px,
    "md" : 720px,
    "lg" : 960px,
    "xl" : 1200px,
    "xxl" : 1400px,
);

@mixin landscape {
    @media screen and (min-width: 300px) and (orientation: landscape) {
        @content;
    }
};

@mixin xs {
    @media screen and (min-width: map-get($breakpoints, "xs")) {
        @content;
    }
};

@mixin sm {
    @media screen and (min-width: map-get($breakpoints, "sm")) {
        @content;
    }
};

@mixin md {
    @media screen and (min-width: map-get($breakpoints, "md")) {
        @content;
    }
};

@mixin lg {
    @media screen and (min-width: map-get($breakpoints, "lg")) {
        @content;
    }
};

@mixin xl {
    @media screen and (min-width: map-get($breakpoints, "xl")) {
        @content;
    }
};

@mixin xxl {
    @media screen and (min-width: map-get($breakpoints, "xxl")) {
        @content;
    }
};